.about-values {
    margin: 10px 0;
    overflow: hidden;
}
h2 {
    margin-left: 15px;
}
.wrapper.inner-thin {
    padding-left: 40px;
    padding-right: 40px;
}

.content-grid-1x1 {
    display: grid;
    grid-template-columns: 1fr 1fr;
   gap: 20px;
    margin-left: 40px;
    font-size: 20px;
}

.grid-cell {
    position: relative;
}


.fullwidth-img__wrap {
    margin: 30px 0;
    height: max-content;
    width: 100%;
}
.fullwidth-img__wrap img {
    width: 100%;
}
.grid-cell__img-wrap {
    width: 100%;
    margin: 30px 0;
    height: max-content;
}
.grid-cell__img-wrap img {
    width: 100%;
}

/* Стили для мобильных устройств */
@media (max-width: 768px) {
    .wrapper.inner-thin {
        padding-left: 20px;
        padding-right: 20px;
    }

    .content-grid-1x1 {
        grid-template-columns: 1fr;
        gap: 10px;
        margin-left: 0;
        font-size: 18px;
    }

    .grid-cell {
        margin: 10px 0;
    }

    .fullwidth-img__wrap,
    .grid-cell__img-wrap {
        margin: 20px 0;
    }
}
