.product-list {
    display: flex;
    flex-wrap: wrap;
    margin: 20px;
    padding: 20px;

    justify-content: space-between;
}

.product-list-item {
    flex: 0 0 calc(33.33% - 10px);
    margin: 5px;
    background-color: #fff;
    text-align: center;
    box-sizing: border-box;
    color: black;
}

.product-box {
    width: 100%;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
}

h2 {
    margin-left: 15px;
}
.more {
    justify-content: end;
    display: flex;
    margin-right: 45px;
}

/* Стили для мобильных устройств */
@media (max-width: 768px) {
    .product-list {
        flex-direction: column;
        align-items: center;
    }

    .product-list-item {
        flex: 0 0 100%;
        margin: 5px 0;
    }

    .more {
        justify-content: center;
        margin: 10px 0;
    }
}

